$(document).scroll(function() {
	checkOffset();
});

function checkOffset() {
	if($('#request').offset().top + $('#request').height() >= $('#footer').offset().top - 10) {
		$('#request').css('position', 'absolute');
	}
	if($(document).scrollTop() + window.innerHeight < $('#footer').offset().top) {
		$('#request').css('position', 'fixed');
	}
}